@import url(https://fonts.googleapis.com/css?family=Lato|Open+Sans&display=swap);
nav {
    background-color: black;
    display: flex;
    justify-content: space-between;
    height: 9vh;
}

.logo {
    z-index: 10;
}

nav h1 {
    padding: .5em;
    z-index: 10;
}

.nav-li {
    color: white;
    text-decoration: none !important;
}

.nav-li:hover {
    color: orange;
}

.nav-items {
    display: flex;
    justify-content: flex-end;
    align-items: baseline;
}

.nav-items li {
    list-style-type: none !important;
    padding: .5em;
    margin-right: 2em;
    margin-top: 1.5em;
}
.city-card{
    display: flex;
    justify-content: space-between;
    width: 30vw;
    border: 1px solid rgb(240, 240, 240);
    border-radius: 5px;
    margin-bottom: 1em;
}

.city-card p{
    padding: 0 1em;
}

.city-card:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-transition: box-shadow .9s ease-in-out;
    transition: box-shadow .9s ease-in-out;
}

.city-card:hover::after{
    -webkit-transition: ease-out;
    transition: ease-out;
}

.city-card img{
    height:10vh;
    width: 10vw;
}
.aside {
    background-color: lightcoral;
    overflow: scroll;
    height: 90vh;
}

.aside-header {
    margin: 1em .5em;
}

.asides-cities {
    display: flex;
    flex-direction: column;
    align-items: center;
}

a {
    color: black;
    text-decoration: none;
}
.city-posts{
    overflow: scroll;
}

.city-header {
    display: flex;
    margin-top: 10px;
    font-size: 2em;
    flex-direction: column;
    justify-content: center;
}

.city-header h2,
h5 {
    margin: 10px;
    text-align: center;
}

.city-image {
    margin-top: 2em;
}

.city-image img {
    height: 30vh;
    width: 100%;
    top: 0;
}

.posts-header {
    display: flex;
    justify-content: space-between;
}
.posts-header h2 {
    margin-top: .5em;
}

.post-btn {
    font-size: 3em;
    margin-right:1em;
}


.post-box {
    display: grid;
    grid-template-columns: 1fr 5fr;
    border: 1px solid rgb(241, 241, 241);
    border-radius: 5px;
    margin: 1em 2em;
}

.post-box:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-transition: .8s;
    transition: .8s;
}

.post-box-image {
    grid-column-start: 1;
    grid-column-end: 2;
    height: 10vh;
    width: 12vw;
    border: 1px solid black;
    border-radius: 4px;
    margin: .5em 2em .5em .5em;
}

.post-box-image img {
    height: 100%;
    width: 100%;
}

.post-box-content {
    grid-column-start: 2;
    grid-column-end: 3;
    margin-right: 2em;
}

.posts-container{
    overflow: scroll;
}
.cities-container{
    display: grid;
    grid-template-columns: 4fr 8fr;
    height:90vh;
}

.aside{
    grid-column-start: 1;
    grid-column-end: 2;
    height: 100vh;
}

.city-posts{
    grid-column-start: 2;
    grid-column-end: 3;
}

.add-post{
    top:20%;
    left:25%;
    height:50vh;
    width: 50vw;
    background-color: white;
    border: 1px solid black;
    position: fixed;
    z-index: 1;
    
}

.add-post form{
    display: flex;
    flex-direction: column;
}

.add-post label{
    font-weight: bold;
}
.add-post button{
    margin-top:1em;
    height:2em;
    width: 5em;
}

#add-post-content{
    height:15vh;
}
.profile-heading {
    text-align: center;
}

.profile-container {
    display: grid;
}
.post-container{
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
}

.post{
    grid-column-start: 2;
    grid-column-end: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid rgb(240, 240, 240);
    margin: 2em 0;
    padding: 1em 0;
    border-radius: 4px;
}
.post-container{
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
}

.post{
    grid-column-start: 2;
    grid-column-end: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid rgb(240, 240, 240);
    margin: 2em 0;
    padding: 1em 0;
    border-radius: 4px;
}
.login {
    top: 20%;
    left: 25%;
    width: 50%;
    background-color: white;
    border: 1px solid black;
    opacity: .9;
    border-radius: 4px;
    position: fixed;
}

.login .submit {
    border: 1px solid black;
    border-radius: 4px;
    background: #1b4079;
    color: white;
    border: none;
    padding: 10px
}
.signup {
    top: 10%;
    left: 25%;
    width: 50%;
    background-color: white;
    border: 1px solid black;
    opacity: .9;
    border-radius: 4px;
    position: fixed;
}

form {
    padding: 1em;
}

.row h2 {
    margin-left: 18rem;
}

.form-control {
    padding: .8em;
    border: 1px solid #1b4079;
    border-radius: 4px;
    margin: 1em;
}

.signup .submit {
    border: 1px solid black;
    border-radius: 4px;
    background: #1b4079;
    color: white;
    border: none;
    padding: 10px;
}

.exit-form {
    border: 1px solid black;
    border-radius: 0 0 4px 4px;
    background: #1b4079;
    color: white;
    border: none;
    padding: .5em;
    position: absolute;
    top: 0;
    right: 0;
}
:root {
    --seagreen-dark-cerulean: #1b4079;
    --dark-onyx: #373E40;
    --seagreen-teal-blue: #4d7c8a;
    --seagreen-dark-sea-green: #87ad88;
    --seagreen-medium-spring-bud: #cbdf90;
    --white: white;
    --winterfreen: #488286;
    --pale-aquea: #B7d5d4;
}

body {
    background-color: white;
    background-color: var(--white);
}

.home-carousel {
    height: 60vh;
    position: relative;
}

.carousel-image-container {
    width: 100%;
    height: 60vh;
    position: relative;
}

.carousel-image {
    width: 100%;
    height: 60vh;
    position: relative;
    z-index: -10;
}

.home-body h2,
h1 {
    display: flex;
}

.home-body h1 {
    display: flex;
    justify-content: center;
}

.heading {
    margin-top: 1%;
}

.home-content {
    width: 100%;
    padding: 1% 2em;
    margin: 0;
}

.home-body p,
h1,
h2 {
    margin-left: 1em;
    margin-right: 1em;
}

.arrow {
    position: absolute;
    border: solid white;
    border: solid var(--white);
    border-width: 0 4px 4px 0;
    padding: 5px;
    top: 50%;
    z-index: 10;
    size: 100;
    margin: 5;
    width: 25;
    height: 25;
}

.left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    left: 2%;
}

.right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    right: 2%;
}

@media (min-width: 1224px) {
    .home-content {
        display: grid;
        grid-template-columns: 3fr 3fr 3fr;
    }
}

;
@media (min-width: 768px) {
    .home-content {
        display: flex;
        flex-direction: column;
    }
}
body {
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    box-sizing: border-box;
}

h1 {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
}

li {
    text-decoration: none;
}

.cities-container a:hover{
    text-decoration: none;
    color:white;
}

i:hover{
    cursor: pointer;
}
